  display: inline-block;
  /* [1] */
  vertical-align: middle;
  /* [2] */
  font: inherit;
  /* [3] */
  text-align: center;
  /* [4] */
  cursor: pointer;
  /* [5] */
  padding: 0.5rem 1rem;
  transition: all 300ms ease-in-out;
  border-radius: 6px;
  text-transform: capitalize;
  font-size: 1.125em;
  font-weight: 500;
  line-height: 1.16667em;
}

/* Style variants
   ========================================================================== */
.c-btn--primary {
  background-color: #614dff;
}

.c-btn--primary, .c-btn--primary:hover, .c-btn--primary:active, .c-btn--primary:focus {
  text-decoration: none;
  /* [4] */
  color: #fff;
}

.c-btn--primary:hover, .c-btn--primary:focus {
  background-color: #212b35;
}

/* Size variants
   ========================================================================== */
.c-btn--small {
  padding: 0.25rem 0.5rem;
}

.c-btn--large {
  padding: 1.05556em 0.94445em 1.16667em;
}

/* Animations
========================================================================== */
.c-btn__animation {
  box-sizing: content-box;
  padding: 0 0.6em;
}

.c-btn__arrow {
  stroke: #212b35;
  stroke-width: 0.375em;
  stroke-dasharray: 400;
  stroke-dashoffset: 400;
  animation: draw 2s alternate infinite;
  transition: all 300ms ease-in-out;
}

.c-btn__arrow-end {
  stroke: #212b35;
  stroke-width: 0.3125em;
  stroke-dasharray: 55;
  stroke-dashoffset: 55;
  animation: draw2 2s alternate infinite;
  transition: all 300ms ease-in-out;
}

.c-btn__strokes {
  fill: #212b35;
  fill-rule: evenodd;
  transform-origin: center;
  transition: all 300ms ease-in-out;
}

.c-btn__strokes--left {
  animation: path1 2s alternate infinite;
}

.c-btn__strokes--right {
  animation: path2 2s alternate infinite;
}

